import axios from "axios";

export default {
    async getAxiosAction(url, data) {
        console.log(url, data);
        try {
            const response = await axios.get(url, {
                params: data
            });
            if (response.data.code != 1) {
                throw new Error(response.data.msg);
            }
            return response.data;
        }
        catch (e) {
            let error = {"code": "-1", "msg": e.message};
            
            console.log("e:", error);
            return e.message;
            //ErrorInfo(e.message);
            // window.postMessage(JSON.stringify(error));
        }
    },
    //获取首页信息
    getHomeInfo(base_url, token, uid, menu = 1, page = 1) {
        let url = `${base_url}api/public/`;
        let data = {
            "service": "ScoreShop.getHome",
            "token": token,
            "uid": uid,
            "menuid": menu,
            "page": page,
            "num": 100,
        };
        return this.getAxiosAction(url, data);
    },
    //获取菜单信息
    getMenuInfo(base_url, token, uid) {
        let url = `${base_url}api/public/`;
        let data = {
            "service": "ScoreShop.getMenu",
            "token": token,
            "uid": uid,
        };
        return this.getAxiosAction(url, data);
    },
    //获取banner信息
    getBannerInfo(base_url, token, uid) {
        let url = `${base_url}api/public/`;
        let data = {
            "service": "ScoreShop.getBanner",
            "token": token,
            "uid": uid,
        };
        return this.getAxiosAction(url, data);
    },
    //获取兑换记录
    getConvertRecordList(base_url, token, uid) {
        let url = `${base_url}api/public/`;
        let data = {
            "service": "ScoreShop.convertRecordList",
            "token": token,
            "uid": uid,
        };
        return this.getAxiosAction(url, data);
    },
    //兑换商品
    exConvertGoods(base_url, token, uid, goodId, type/*, price, score, payType = null*/) {
        let url = `${base_url}api/public/`;
        let data = {
            "service": "ScoreShop.convertGoods",
            "token": token,
            "uid": uid,
            "goods_id": goodId,
            "wxpay_type": type,
            //"pay_type": payType,
            //"price": price,
            //"score": score,
        };
        return this.getAxiosAction(url, data);
    },
    //查询订单是否支付完成
    exQueryOrder(base_url, orderno, wxpay_type = '') {
        let url = `${base_url}api/public/`;
        let data = {
            "service": "ScoreShop.queryOrder",
            "orderno": orderno,
            wxpay_type
        };
        return this.getAxiosAction(url, data);
    },
    //获取订单详情
    getConvertRecord(base_url, token, uid, id) {
        let url = `${base_url}api/public/`;
        let data = {
            "service": "ScoreShop.convertRecord",
            "token": token,
            "uid": uid,
            "recordid": id
        };
        return this.getAxiosAction(url, data);
    },
}
