<!--
    兑换记录详情
-->

<template>
    <div class="order-wrapper">
        <back-title :title="this.$route.name"></back-title>
        <div class="msg">
            <p class="msg-text"><i class="icon" :class="orderStatus==2?'icon-chenggong':'icon-cuowu'"></i>{{orderStatus==2?"恭喜，兑换成功":"兑换失败"}}
            </p>
        </div>
        <p class="order-status">
            <span class="status-text">订单状态：</span>
            <span class="status-text">{{orderText(orderStatus)}}</span>
        </p>
        <div class="order-detail">
            <div class="order-icon" v-if="exchangeDetail.goods">
                <img class="img" :src=exchangeDetail.goods.icon alt="">
                <p class="name">{{exchangeDetail.goods.name}}</p>
            </div>
            <p class="item">
                <span>支付金额：</span>
                <span class="charges">{{exchangeDetail.price}}</span>
            </p>
            <p class="item">
                <span>订单编号：</span>
                <span>{{exchangeDetail.oid}}</span>
            </p>
            <p class="item">
                <span>下单时间：</span>
                <span>{{exchangeDetail.time}}</span>
            </p>
        </div>
        <div class="order-desc">
            <h2 class="title">商品详情</h2>
            <h2 class="title">活动规则</h2>
            <p class="text">这里加入虚拟商品的兑换规则等内容</p>
            <h2 class="title">详情说明</h2>
            <p class="text">这里填写你的虚拟商品说明</p>
        </div>
        <div class="index-icon" @click="goHome">
            <i class="icon icon-shouye"></i>
        </div>
    </div>
</template>

<script>
    import {mapGetters} from "vuex";
    import BaseService from "../api/BaseService";
    import {orderStatus} from "../assets/js/common";
    import BackTitle from "../base/BackTitle";

    export default {
        name: "ExchangeDetail",
        data() {
            return {
                exchangeDetail: [],
                userData: {},
                isOrder: true,
                newOrder: "",
                orderId: "",
                orderStatus: 0,
            }
        },
        created() {
            this.userData = JSON.parse(window.localStorage.getItem("__ZSWWData__"));
            this.newOrder = JSON.parse(window.localStorage.getItem("__ORDER__"));
            this.orderId = window.localStorage.getItem("__ORDERID__");
        },
        mounted() {
            if (!this.newOrder) {
                this.$router.back();
            }
            if (this.newOrder) {
                console.log(this.$route.query);
                this._getConvertRecord(this.newOrder);
            }
            if (this.orderId !== "") {
                console.log("this.$route.query.scoreSpend", this.$route.query.scoreSpend);
                console.log("this.$route.query.scoreSpend", typeof this.$route.query.scoreSpend);

                if (!this.$route.query.scoreSpend) {//如果积分足够则不查询微信订单
                    console.log("----------", !!this.browser.versions.weixin);
                    if (this.browser.versions.weixin) {
                        console.log("**********");
                        this._exQueryOrder(this.orderId, 'JSAPI');
                        if (this.isOrder) {
                            this.timer = setInterval(() => {
                                this._exQueryOrder(this.orderId, 'JSAPI');
                            }, 3000);
                        }
                    } else {
                        console.log("----------");
                        this._exQueryOrder(this.orderId);
                        if (this.isOrder) {
                            this.timer = setInterval(() => {
                                this._exQueryOrder(this.orderId);
                            }, 3000);
                        }
                    }
                } else {
                    console.log('支付成功');
                }
            }

        },
        destroyed() {
            this.isOrder = false;
            clearInterval(this.timer);
        },
        methods: {
            //本地订单查询
            async _getConvertRecord(id) {
                let result = await BaseService.getConvertRecord(this.userData.base_url, this.userData.token, this.userData.uid, id);
                if (result.code == 1) {
                    this.exchangeDetail = result.data;
                    this.orderStatus = parseInt(result.data.status);
                } else {
                    this.$message.error(result);
                }
            },
            //微信订单查询
            async _exQueryOrder(order, wxpayType = '') {
                if (this.orderStatus != 2) {
                    let result = await BaseService.exQueryOrder(this.userData.base_url, order, wxpayType);
                    if (result.code == 1) {
                        if (result.data.status == 2) {
                            this.orderStatus = 2;
                            clearTimeout(this.timer);
                            this.isOrder = false;
                        }
                    } else {
                        this.$message.error(result);
                    }
                } else {
                    clearTimeout(this.timer);
                }
            },
            orderText(status) {
                return orderStatus(status)
            },
            goHome() {
                this.$router.push({
                    path: `/index?base_url=${this.userData.base_url}&token=${this.userData.token}&uid=${this.userData.uid}`
                });
                clearInterval(this.timer);
            }
        },
        computed: {
            ...mapGetters(["exchangeId", "prepayId"])
        },
        components: {
            BackTitle
        }
    }
</script>

