<!--
    商品详细信息
-->
<template>
    <div class="detail_wrapper">
        <back-title :title="this.$route.name"></back-title>
        <div class="detail_image">
            <img :src=details.goods_banner alt="">
        </div>
        <div class="detail_content">
            <div class="detail_ctn">
                <h3 class="detail_title">{{details.goods_name}}</h3>
                <div class="detail_score" v-if="details.exchange_info">
                    <p v-if="details.exchange_info.score">
                        <span>{{details.exchange_info.score}}</span>
                        <span class="score">积分</span>
                    </p>
                    <span class="and" v-if="details.exchange_info.score && details.exchange_info.price">+</span>
                    <p v-if="details.exchange_info.price">
                        <span class="score">￥</span>
                        <span>{{parseInt(details.exchange_info.price)/100}}</span>
                    </p>
                </div>
                <p class="detail_score now-score">我的积分：{{integral}}</p>
            </div>
        </div>
        <div class="footer" v-if="parseInt(details.goods_stock)">
            <p @click="showConfirm" class="button" :class="{'active':isEnough}">{{isEnough?'兑换':'积分不足'}}</p>
        </div>
        <div class="footer" v-if="!parseInt(details.goods_stock)">
            <p class="button">库存不足</p>
        </div>
        <div class="confirm-window" v-if="isShowConfirm">
            <div class="content">
                <p class="desc">你确定兑换！</p>
                <div class="confirm-button">
                    <p class="cancel" @click="cancelClick">取消</p>
                    <p class="confirm" @click="confirmClick">确认</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapGetters, mapMutations} from "vuex";
    import BaseService from "../api/BaseService";
    import BackTitle from "../base/BackTitle";
    
    export default {
        name: "ItemDetails",
        data() {
            return {
                mode: "1",
                orderId: "",
                isIntegral: true,
                isShowConfirm: false,
                userData: {},
            }
        },
        created() {
            this.userData = JSON.parse(window.localStorage.getItem("__ZSWWData__"));
            if (!this.details.id) {
                this.$router.push({
                    path: `/index?base_url=${this.userData.base_url}&token=${this.userData.token}&uid=${this.userData.uid}`
                })
            }
        },
        mounted() {
            /*window.document.addEventListener('message', (e) => {
                let result = JSON.parse(e.data);
                if (result.msg === "orderStatus") {
                    if (this.orderId) {
                        this.setExchange(this.orderId);
                        this.$router.push({
                            path: `/exchange/${this.orderId}`
                        })
                    }
                }
            });*/
        },
        computed: {
            ...mapGetters(["details", "integral"]),
            isEnough() {
                if (this.details.goods_type === "redpack") {
                    if (parseInt(this.details.exchange_info.score) > this.integral) {
                        return false;
                    } else {
                        return true;
                    }
                }
                return true;
            },
        },
        methods: {
            ...mapMutations({
                "setExchange": "SET_EXCHANGE",
                "setIntegral": "SET_INTEGRAL",
                "setPrepayId": "SET_PREPAY_ID",
            }),
            showConfirm() {
                if (this.isEnough) {
                    this.isShowConfirm = true;
                }
            },
            cancelClick() {
                this.isShowConfirm = false;
            },
            confirmClick() {
                this.isShowConfirm = false;
                this.exProject();
            },
            async exProject() {
                try {
                    console.log(this.userData.base_url, this.userData.token, this.userData.uid, this.details);
                    let scoreSpend = this.details.exchange_info.new_price === this.details.exchange_info.score;
                    let type = null;
                    if (this.browser.versions.weixin) {
                        type = "JSAPI";
                    }
                    let result = await BaseService.exConvertGoods(this.userData.base_url, this.userData.token, this.userData.uid, this.details.id, type);

                    console.log("result:", result);
                    if (this.browser.versions.weixin) {
                        this.setPrepayId(result.data.prepay_id);
                    }
                    if (result.code != 1) {
                        this.$message({
                            type: "error",
                            message: result,
                            duration: 2000,
                        })
                    } else {
                        if (result.data.score) {
                            //vuex保存当前积分
                            this.setIntegral(result.data.score);
                        }
                        this.orderId = result.data.recordid;
                        if (this.orderId) {
                            //保存兑换ID和本地订单号
                            this.setExchange(this.orderId);
                            window.localStorage.setItem("__ORDER__", result.data.recordid);
                            window.localStorage.setItem("__ORDERID__", result.data.oid);


                            console.log("是否是微信浏览器", this.browser.versions.weixin);
                            if (this.browser.versions.weixin) {
                                //微信浏览器拉取支付
                                window.parent.pay(JSON.stringify(result.data));
                            } else {
                                //非微信浏览器拉起支付
                                window.location.href = result.data.mweb_url;
                            }
                            //跳转订单详情
                            this.$router.push({
                                path: `/exchange/${this.orderId}`,
                                query: {
                                    base_url: this.userData.base_url,
                                    token: this.userData.token,
                                    uid: this.userData.uid,
                                    order: result.data.oid,
                                    scoreSpend
                                }
                            })
                        }
                    }
                } catch (e) {
                    console.log(e.message);
                }
            }
        },
        components: {
            BackTitle
        }
    }
</script>

