<template>
    <div class="exchange">
        <back-title :title="this.$route.name"></back-title>
        <div>
            <ul class="wrapper">
                <li class="exchange-item"
                    v-for="score in scoreHistory"
                    :key="score.oid"
                    @click="exchangeItem(score)"
                >
                    <div class="img">
                        <img v-if="score.goods" class="icon" :src=score.goods.icon alt="">
                    </div>
                    <div class="content">
                        <p class="name">{{score.goods.name}}</p>
                        <p class="status">{{orderText(score.status)}}</p>
                    </div>
                    <p class="join-icon">></p>
                </li>
                <p class="footer-text">已经没有更多了！</p>
            </ul>
        </div>
    </div>
</template>

<script>
    import {mapMutations} from "vuex";
    import BaseService from "../api/BaseService";
    import {orderStatus} from "../assets/js/common";
    import backTitle from "../base/BackTitle";

    export default {
        name: "Exchange",
        data() {
            return {
                scoreHistory: [],
                userData: {},
            }
        },
        created() {
            this.userData = JSON.parse(window.localStorage.getItem("__ZSWWData__"));
        },
        mounted() {
            this._getConvertRecordList();
        },
        methods: {
            async _getConvertRecordList() {
                console.log(this.userData);
                let result = await BaseService.getConvertRecordList(this.userData.base_url, this.userData.token, this.userData.uid);
                console.log(result.data);
                this.scoreHistory = result.data;
            },
            exchangeItem(item) {
                console.log(item);
                this.setExchange(item.recordid);
                window.localStorage.setItem("__ORDER__", item.recordid);
                window.localStorage.setItem("__ORDERID__", "");
                this.$router.push({
                    path: `/exchange/${item.recordid}`
                })
            },
            orderText(status) {
                return orderStatus(status)
            },
            ...mapMutations({
                "setExchange": "SET_EXCHANGE"
            })
        },
        components: {
            backTitle,
        }
    }
</script>

