<template>
    <div id="app">
        <router-view v-if="isRouterAlive"/>
    </div>
</template>

<script>
    import {mapMutations} from "vuex";
    import {browser} from "./assets/js/common";

    export default {
        data() {
            return {
                isRouterAlive: true
            }
        },
        created() {
            if (!browser.versions.webApp) {
                //window.location.href = "myapp://";
            }
            let newData = this.$route.query;
            if (newData.token) {
                let oldData = JSON.parse(window.localStorage.getItem("__ZSWWData__"));
                if (window.localStorage.getItem("__ZSWWData__") === "{}" || oldData == null) {
                    if (oldData != null) {
                        if (!oldData.token) {
                            if (newData.token) {
                                let data = {
                                    base_url: newData.base_url,
                                    token: newData.token,
                                    uid: newData.uid,
                                };
                                window.localStorage.setItem("__ZSWWData__", JSON.stringify(data));
                            }
                        }
                    } else {
                        console.log(newData.token);
                        if (newData.token) {
                            let data = {
                                base_url: newData.base_url,
                                token: newData.token,
                                uid: newData.uid,
                            };
                            window.localStorage.setItem("__ZSWWData__", JSON.stringify(data));
                        }
                    }
                } else {
                    if (newData.token) {
                        if (oldData.token !== newData.token) {
                            let data = {
                                base_url: newData.base_url,
                                token: newData.token,
                                uid: newData.uid,
                            };
                            window.localStorage.setItem("__ZSWWData__", JSON.stringify(data));
                        }
                    }
                }
            }
        },
        methods: {
            ...mapMutations({
                "setBaseUrl": "SET_BASE_URL",
                "setToken": "SET_TOKEN",
                "setUid": "SET_UID",
                "setIntegral": "SET_INTEGRAL"
            }),
            reload() {
                this.isRouterAlive = false
                this.$nextTick(() => (this.isRouterAlive = true))
            }
        },
    }
</script>
