<!--
    商品列表
-->

<template>
    <ul class="list">
        <li class="list_item" v-for="item in listData" :key="item.id" @click="itemDetail(item)">
            <div class="img">
                <img class="item_img" :src=item.goods_pic alt=item.title>
                <p v-if="!parseInt(item.goods_stock)" class="item-stock">{{parseInt(item.goods_stock)?"":"暂无库存"}}</p>
            </div>
            <p class="item_title">{{item.goods_name}}</p>
            <div class="item_introduce">
                <p v-if="item.exchange_info.score" class="item_need">{{item.exchange_info.score}} 积分</p>
                <span v-if="item.exchange_info.score && item.exchange_info.price">+</span>
                <p v-if="item.exchange_info.price" class="item_need">{{parseInt(item.exchange_info.price)/100}} 元</p>
            </div>
        </li>
    </ul>
</template>

<script>
    import {mapMutations} from "vuex";

    export default {
        name: "listItem",
        props: {
            listData: {
                type: Array,
                required: true,
                userData: {},
            },
        },
        created() {
            this.userData = JSON.parse(window.localStorage.getItem("__ZSWWData__"));
        },
        methods: {
            itemDetail(item) {
                this.set_details(item);
                this.$router.push({
                    path: `/detail/${item.id}`
                })
            },
            ...mapMutations({
                "set_details": "SET_DETAILS",
            })
        },
    }
</script>

