import Vue from 'vue'
import Router from 'vue-router'
import Home from './components/Home.vue'
import Exchange from './components/ExchangeList'
import ExchangeDetail from './components/ExchangeDetail'
import ItemDetails from './components/ItemDetails';

Vue.use(Router)

export default new Router({
    mode: 'hash',
    routes: [
        {
            path: '',
            name: 'index',
            redirect: '/index',
        },
        {
            path: '/index',
            name: '首页',
            component: Home
        },
        {
            path: '/exchange',
            name: '兑换记录',
            component: Exchange,
        },
        {
            path: '/exchange/:id',
            name: '兑换记录详情',
            component: ExchangeDetail
        },
        {
            path: '/detail/:id',
            name: '商品详情',
            component: ItemDetails,
        }
    ]
})
