import * as types from "./mutation-types";

const mutations = {
    [types.SET_BASE_URL](state, base_url) {
        state.base_url = base_url;
    },
    [types.SET_TOKEN](state, token) {
        state.token = token;
    },
    [types.SET_UID](state, uid) {
        state.uid = uid;
    },
    [types.SET_INTEGRAL](state, integral) {
        state.integral = integral;
    },
    [types.SET_DETAILS](state, details) {
        state.details = details
    },
    [types.SET_EXCHANGE](state, exchange) {
        state.exchangeId = exchange
    },
    [types.SET_PREPAY_ID](state, id) {
        state.prepayId = id
    }
};

export default mutations;