<template>
    <div class="home-wrapper">
        <nav>
            <a href="javascript:;"><i class="icon icon-jifen"></i>积分 <strong
                    class="score_num">{{integral}}</strong></a>
            <a href="javascript:;" @click="goExchange"><i class="icon icon-jilu"></i>兑换记录</a>
        </nav>
        <div class="swiper-container" v-if="banners.length">
            <swiper :options="swiperOption" ref="mySwiper" v-if="banners.length"
                    @click.native="clickBanner(swiper.activeIndex-1)">
                <!-- slides -->
                <swiper-slide v-for="(item) in banners" :key="item.id">
                    <img class="slide_item" :src="item.img" alt="">
                </swiper-slide>
                <!-- Optional controls -->
                <div class="swiper-pagination" slot="pagination" v-if="banners.length>1"></div>
            </swiper>
        </div>

        <div class="menu" v-if="menus.length">
            <swiper class="nav-wrapper" :options="swiperContent" :class="{'swiper-no-swiping':menus.length < 4}">
                <swiper-slide :key="menu.id"
                              @click.native="menuClick(menu)"
                              v-for="menu in menus"
                              class="menu-item"
                              :class="{active:menu.is_default==1}">
                    {{menu.name}}
                </swiper-slide>
            </swiper>
        </div>
        <div class="list-wrapper">
            <ListItem :listData="listData" :score="score"/>
            <p class="footer-text">{{footerMsg}}</p>
        </div>
    </div>
</template>

<script>
    import {swiper, swiperSlide} from 'vue-awesome-swiper'
    import 'swiper/dist/css/swiper.css';
    import {mapGetters, mapMutations} from "vuex";
    import ListItem from "./ListItem"
    import BaseService from "../api/BaseService";
    
    export default {
        name: "home",
        data() {
            return {
                footerMsg: "加载更多...",
                isAnyData: true,
                banners: [],
                menus: [],
                listData: [],
                userData: {},
                score: 0,
                swiperOption: {
                    pagination: {
                        el: '.swiper-pagination'
                    },
                    slidesPerView: 1,
                    autoplay: {
                        delay: 3000,
                        disableOnInteraction: false
                    },
                    spaceBetween: 30,
                    loop: true,
                },
                swiperContent: {
                    slidesPerView: 3,
                    spaceBetween: 30,
                    freeMode: true,
                }
            }
        },
        mounted() {
            this.userData = JSON.parse(window.localStorage.getItem("__ZSWWData__"));
            //new VConsole();
            this._getMenuInfo();
            this._getBannerInfo();
        },
        methods: {
            async _getHomeInfo(menuid = 1, page = 1) {
                let result = await BaseService.getHomeInfo(this.userData.base_url, this.userData.token, this.userData.uid, menuid, page);
                this.score = result.data.score;
                this.setIntegral(result.data.score);
                window.localStorage.setItem("__SCORE__", result.data.score);            //保存积分
                this.listData = result.data.goods;
            },
            async _getMenuInfo() {
                let result = await BaseService.getMenuInfo(this.userData.base_url, this.userData.token, this.userData.uid);
                if (result.code == 1) {
                    this.menus = result.data;
                    for (let i in result.data) {
                        if (result.data[i].is_default == 1) {
                            this._getHomeInfo(result.data[i].id);
                        }
                    }
                } else {
                    this.$message.error(result);
                }
            },
            async _getBannerInfo() {
                let result = await BaseService.getBannerInfo(this.userData.base_url, this.userData.token, this.userData.uid);
                if (result.code == 1) {
                    console.log('banner', result);
                    this.banners = result.data;
                } else {
                    this.$message.error(result);
                }
            },
            goExchange() {
                this.$router.push({
                    path: `/exchange?base_url=${this.userData.base_url}&token=${this.userData.token}&uid=${this.userData.uid}`
                })
            },
            menuClick(menu) {
                console.log(menu);
                this._getHomeInfo(menu.id);
                let data = this.menus;
                for (let i in data) {
                    data[i].is_default = 0;
                    if (data[i].id === menu.id) {
                        console.log(data[i].id);
                        data[i].is_default = 1;
                    }
                }
                this.menus = data;
            },
            clickBanner(index) {
                console.log(index);
                let dataIndex = index % this.banners.length;
                if (dataIndex < 0) {
                    dataIndex = this.banners.length - 1;
                }
                console.log(dataIndex);
                console.log(this.banners[dataIndex].url);
                if (this.banners[dataIndex].url) {
                    // window.open(item.url);
                    window.location.href = this.banners[dataIndex].url
                }
            },
            ...mapMutations({"setIntegral": "SET_INTEGRAL"})
        },
        computed: {
            ...mapGetters(["base_url", "token", "uid", "integral"]),
            swiper() {
                return this.$refs.mySwiper.swiper
            }
        },
        components: {
            ListItem,
            swiper,
            swiperSlide
        }
    }
</script>

