import Vue from 'vue'
import App from './App.vue'
import router from "./router";
import store from "./store";
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import BaseService from "./api/BaseService";
import {getQueryVariable} from "./assets/js/common";

Vue.config.productionTip = false;
Vue.use(ElementUI);

if (getQueryVariable('base_url')) {
    let base_url = getQueryVariable('base_url');
    let token = getQueryVariable('token');
    let uid = getQueryVariable('uid');
    let oldData = JSON.parse(window.localStorage.getItem("__ZSWWData__"));
    if (window.localStorage.getItem("__ZSWWData__") === "{}" || oldData == null) {
        if (oldData != null) {
            if (!oldData.token) {
                if (token) {
                    let data = {
                        base_url: base_url,
                        token: token,
                        uid: uid,
                    };
                    window.localStorage.setItem("__ZSWWData__", JSON.stringify(data));
                }
            }
        } else {
            console.log(token);
            if (token) {
                let data = {
                    base_url: base_url,
                    token: token,
                    uid: uid,
                };
                window.localStorage.setItem("__ZSWWData__", JSON.stringify(data));
            }
        }
    } else {
        if (token) {
            if (oldData.token !== token) {
                let data = {
                    base_url: base_url,
                    token: token,
                    uid: uid,
                };
                window.localStorage.setItem("__ZSWWData__", JSON.stringify(data));
            }
        }
    }
}

Vue.prototype.browser = {
    versions: function () {
        let u = navigator.userAgent;
        return {
            trident: u.indexOf('Trident') > -1, //IE内核
            presto: u.indexOf('Presto') > -1, //opera内核
            webKit: u.indexOf('AppleWebKit') > -1, //苹果、谷歌内核
            gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1,//火狐内核
            mobile: !!u.match(/AppleWebKit.*Mobile.*/), //是否为移动终端
            ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios终端
            android: u.indexOf('Android') > -1 || u.indexOf('Adr') > -1, //android终端
            iPhone: u.indexOf('iPhone') > -1, //是否为iPhone或者QQHD浏览器
            iPad: u.indexOf('iPad') > -1, //是否iPad
            webApp: u.indexOf('Safari') == -1, //是否web应该程序，没有头部与底部
            weixin: u.indexOf('MicroMessenger') > -1, //是否微信 （2015-01-22新增）
            qq: u.match(/\sQQ/i) == " qq" //是否QQ
        };
    }(),
    language: (navigator.browserLanguage || navigator.language).toLowerCase()
};


new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')
